.hairtsagg {
  background-color: rgb(50, 50, 187);
  width: 300px;
  height: 200px;
  border-radius: 30px;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 20px;
}
.containerr {
  display: flex;
  justify-content: center;
  gap: 10px;
}
.blue {
  background-color: rgb(0, 255, 55);
  padding: 12px;
  padding-inline: 30px;
  border-radius: 20px;
}
.orange {
  background-color: rgb(255, 81, 0);
  padding: 12px;
  padding-inline: 30px;
  border-radius: 20px;
}
.green {
  background-color: rgb(31, 172, 31);
  padding: 12px;
  padding-inline: 30px;
  border-radius: 20px;
}

.hairtsagg h1 {
  font-size: 60px;
  font-weight: bold;
  margin: 0px;
}
.hairtsagg p {
  font-size: 32px;
}
.darah-hesegg:active {
  transform: scale(0.97);
}
.darah-hesegg {
  border: 1px solid black;
  height: 65vh;
  width: 50vw;
  cursor: pointer;
  margin-top: 20px;
  margin-inline: 20px;
  border-radius: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 55px;
  transition: 0.5s;
}
.mainn {
  display: flex;
  flex-direction: column;
  align-items: center;
}
